@import '../../theme/vars.css';

.appleButton {
  all: initial;
  background-color: #262626;
  box-sizing: border-box;
  color: var(--color-white);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  cursor: pointer;
  border-radius: 8px;
  border: none;
  padding: 8px 48px 8px 16px;
  gap: 16px;
  margin-bottom: 16px;
  width: 100%;

  font-family: Inter;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}

.appleButton > span {
  align-items: center;
  display: flex;
  flex: 1 1 100%;
  justify-content: center;
}

.appleButton > svg {
  height: 32px;
  width: 32px;
  cursor: pointer !important;
}
